import { WatchlistDataTableTextCell } from "../../WatchlistDataTableCells/WatchlistDataTableTextCell"
import { genericColumn } from "../../genericColumn"

function acceptedDate(_tableName: string) {
  const title = "Accepted Date"
  return genericColumn({
    endpoint: "companyBalanceStatement",
    columnKeys: ["acceptedDate"],
    size: 130,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "The date and time the filing was accepted.",
    cell: info => {
      return (
        <WatchlistDataTableTextCell
          value={info.row.original.financialData.companyBalanceStatement?.data?.acceptedDate}
          queryResult={info.row.original.financialData.companyBalanceStatement}
        >
          {info.row.original.financialData.companyBalanceStatement?.data?.acceptedDate}
        </WatchlistDataTableTextCell>
      )
    },
  })
}

export { acceptedDate }
